.root {
  width: 50vw;
  height: 28.4vw;
  position: relative;
  background-color: #000000;
  display: -ms-flexbox;
  display: flex;
}

.root .bg {
  position: absolute;
  background-size: 100% auto;
  width: 100%;
  height: 100%;
  opacity: 0.6;
}

.root .titleOne,
.root .titleTwo,
.root .action {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.root .titleOne {
  top: 3.6vw;
  font-size: 2.2vw;
  font-weight: 600;
}

.root .titleTwo {
  top: 11.6vw;
  font-size: 2.8vw;
  font-weight: 600;
  color: #e71d36;
}

.root .action {
  top: 22.6vw;
}

.root .action button {
  font-size: 1.1vw;
  border: 0.15vw #ffffff solid;
  padding: 1.2vw 2.5vw;
  border-radius: 9999px;
  background: transparent;
  outline: none;
  color: #ffffff;
}


